.payment{
	&__info{
		margin-bottom: 30px;
		&-title{
			font-size: 16px;
			line-height: 1.31;
			color: #8494ab;
			margin-bottom: 18px;
		}
		&-row{
			font-size: 16px;
			line-height: 1.31;
			margin-bottom: 7px;
			&:last-child{
				margin-bottom: 0;
			}
			&-title{
				width: 155px;
				display: inline-block;
				color: #8494ab;
			}
			&-val{
				display: inline-block;
				color: #373c43;
			}
		}
	}
	&__card{
		width: 350px;
		height: 236px;
		background: #f7f8f8;
		position: relative;
		border: 1px solid #e4e9ee;
		border-radius: 10px;
		padding: 22px 15px 13px 13px;
		&._front{
  	  z-index: 15;
		}
		&._back{
  	  z-index: 5;
			margin: -215px 0 14px 213px;
		  padding-top: 82px;
			&:before{
				content: '';
				width: 100%;
				height: 42px;
				background: #e4e9ee;
				position: absolute;
		    left: 0;
		    top: 23px;
			}
		}
		&-number{
			margin-bottom: 12px;
		}
		&-date{
			margin-bottom: 12px;
		}
		&-name{

		}
		&-code{
			width: 138px;
			margin-left: auto;
			&-inner{
				position: relative;
			}
		}
	}
	&__form{
		&-label{
			display: block;
			font-size: 16px;
			line-height: 1.31;
			color: #8494ab;
			margin-bottom: 4px;
		}
		&-input{
			height: 42px;
			background-color: #ffffff;
			border: solid 1px #e4e9ee;
			padding: 0 15px;
			&._small{
				width: 65px;
				margin-right: 6px;
				&:last-child{
					margin-right: 0;
				}
			}
			&._all-width{
				width: 100%;
			}
			&:-moz-placeholder {
				color: #bec6cf;
				font-size: 16px;
			}
			&:-ms-input-placeholder {
				color: #bec6cf;
				font-size: 16px;
			}
			&::-webkit-input-placeholder {
				color: #bec6cf;
				font-size: 16px;
			}
		}
		&-select{
			height: 42px;
			background-color: #ffffff;
			border: solid 1px #e4e9ee;
			border-radius: 0;
			font-size: 16px;
			color: #373c43;
		}
		&-question{
			width: 23px;
			height: 23px;
			border-radius: 50%;
			border: none;
			background: url('@{pathToImg}icons/question.png') no-repeat center;
			background-size: contain;
			position: absolute;
			top: 9px;
	    right: 9px;
			padding: 0;
		}
	}
}
